@import "../utilities/variables.scss";

.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    color: $primary-color;
    height: 209px;
    width: 100%;
    gap: 28.56px;

    .logo-footer {
        width: 122px;
        height: 39.4px;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            height: auto;
            filter: brightness(0) invert(1);
        }
    }
}

@media screen and (max-width: 380px) {
    .footer {
        gap: 15px;

        p {
            text-align: center;
            width: 133px;
            font-size: 12px;
        }
    }
}