#global {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: clamp(20px, 2.4086vw + 22.481px, 45px);
    gap: clamp(27px, 2.2474vw + 22.752px, 50px);
    padding: 0px 20px;
    margin-bottom: 50px;
}

@media screen and (max-width: 380px) {
    #global {
        margin-top: 20px;
        gap: 27px;
    }
}